<template>
  <div>
   <div class="adiv">
      <van-icon name="arrow-left" class="myicon" @click="$router.back()" />
      <h1>我的历史浏览</h1>
    </div>
    <van-empty image="error" description="暂无历史浏览" v-show="this.historylist.length==0" />

    
    <van-panel
      v-for="(item, index) in historylist"
      :key="index"
      :title="`第${index + 1}条历史浏览`"
      :desc="
        new Date(parseInt(item.time)).toLocaleString().replace(/:\d{1,2}$/, '')
      "
    > <router-link
          :to="{ path: '/detailed', query: { item_id:item._id,cate_id:item.cate_id} }"
          custom
          v-slot="{ navigate }"
        >
          <span @click="navigate" @keypress.enter="navigate" role="link">
      <div class="con">我的发布标题《{{ item.title }}》</div>
       </span>
        </router-link>
     
    </van-panel>
  </div>
</template>

<script>
import axios from "axios"
export default {
    name:"",
    data() {
        return {
               loginfo: JSON.parse(sessionStorage.getItem("loginfo")),
        historylist:[]

        }
    },
    methods: {
        
    },
    created() {
axios
    .post(
        `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_history_list?uid=${this.loginfo.uid}&skip&limit`
    )
    .then((result) => {
        console.log("result: ", result);
        this.historylist = result.data.data;
      })
      .catch((err) => {
        console.log("err: ", err);
      });
    },

}
</script>

<style lang="scss" scoped>
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;

  color: aliceblue;
  font-size: 14px;
}
h1 {
  margin: 0 auto;
  font-size: 16px;
}
.myicon {
  font-size: 32px;
}
.con {
  font-size: 16px;
  text-indent: 19px;
}
</style>

